<template>
  <div>
    <v-container class="charla__container">
      <title-ribbon :title="charla.Titulo" />
      <div
        v-if="charged"
        class="charla_container__main"
      >
        <v-row class="charla_row__img">
          <v-col class="text-center">
            <v-img
              :src="
                `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto:good/v${ charla.VersionImagen }/terapiacl/charlas-y-talleres/charlas-imagenes-principales/${charla.Imagen}.png`
              "
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charla_row__introduccion">
          <v-col class="cri_main">
            <h3 class="titulo">
              Introducción:
            </h3>
            <p v-html="charla.Introduccion" />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charla_row__objetivos">
          <v-col class="cro_main">
            <h3 class="titulo">
              Objetivos:
            </h3>
            <ul>
              <li
                v-for="(item, i) in charlasObjetivos"
                :key="i"
              >
                {{ item.Descripcion }}
              </li>
            </ul>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charla_row__dirigido">
          <v-col class="crdi_main">
            <h3 class="titulo">
              Dirigido a:
            </h3>
            <span>{{ charla.Dirigido }}</span>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charla_row__detalle">
          <v-col
            v-if="charla.Fecha != null"
            class="col-12 crde_main"
          >
            <ul>
              <li class="li-up">
                <h3>Fecha:</h3>
                {{ charla.Fecha }}
              </li>
              <li>
                <h3>Modalidad:</h3>
                {{ charla.Modalidad }}
              </li>
              <li class="li-up">
                <h3>Hora:</h3>
                {{ charla.Hora }}
              </li>
              <li>
                <h3>Relator/a:</h3>
                {{ charla.Relator }}
              </li>
              <li class="li-up">
                <h3>Lugar:</h3>
                {{ charla.Lugar }}
              </li>
              <li>
                <h3>Valor:</h3>
                {{ charla.Valor }}
              </li>
            </ul>
          </v-col>
          <v-col
            v-else
            class="col-12 crde_main-nodisp"
          >
            <span><strong>Esta actividad no tiene programación por el momento.</strong><br>
              Se informará una vez que se defina fecha, horario y lugar.
            </span>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charla_row__form-title">
          <v-col>
            <h3>Solicita tu inscripción o más información aquí:</h3>
          </v-col>
        </v-row>
        <validation-observer
          ref="observer"
          v-slot="{ invalid }"
        >
          <form
            class="px-0 mx-0 my-0"
            @submit.prevent="validateForm()"
          >
            <v-row class="charla_row__form">
              <v-col>
                <v-row class="charla_row_form_main">
                  <v-col class="col-12 col-md-6 crt-form__col">
                    <validation-provider
                      v-slot="{ errors }"
                      rules="required|max:180"
                      name="Nombre Completo Participante"
                    >
                      <label class="input-label">
                        Nombre Completo Participante:
                      </label>
                      <v-text-field
                        v-model="nombreCompleto"
                        :error-messages="errors"
                        color="success"
                        filled
                        dense
                      />
                    </validation-provider>
                    <validation-provider
                      v-slot="{ errors }"
                      rules="required|rut"
                      name="Rut"
                    >
                      <label class="input-label">
                        Rut Participante
                      </label>
                      <v-text-field
                        v-model="rut"
                        :error-messages="errors"
                        color="success"
                        filled
                        dense
                        :validate-on-blur="false"
                        name="user[rut]"
                        flat
                      />
                    </validation-provider>
                    <validation-provider
                      v-slot="{ errors }"
                      rules="required|max:50"
                      name="Telefono"
                    >
                      <label class="input-label">
                        Teléfono
                      </label>
                      <v-text-field
                        v-model="telefono"
                        :error-messages="errors"
                        color="success"
                        filled
                        dense
                      />
                    </validation-provider>
                  </v-col>
                  <v-col class="col-12 col-md-6 crt-form__col">
                    <validation-provider
                      v-slot="{ errors }"
                      rules="required"
                      name="Fecha de Nacimiento"
                    >
                      <v-menu
                        ref="menuFnac"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="zoom-center-transition"
                        offset-y
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on }">
                          <label class="input-label">
                            Fecha de Nacimiento
                          </label>
                          <v-text-field
                            v-model="computedOrdenarFecha"
                            color="success"
                            name="date"
                            readonly
                            :error-messages="errors[0]"
                            filled
                            dense
                            v-on="on"
                          />
                        </template>
                        <v-date-picker
                          v-model="fechaNacimiento"
                          locale="es-ES"
                          color="success"
                          @input="menuFnac = false"
                        />
                      </v-menu>
                    </validation-provider>
                    <validation-provider
                      v-slot="{ errors }"
                      rules="required|email"
                      name="E-Mail"
                    >
                      <label class="input-label">
                        E-Mail
                      </label>
                      <v-text-field
                        v-model="email"
                        :error-messages="errors"
                        color="success"
                        filled
                        dense
                      />
                    </validation-provider>
                    <validation-provider
                      v-slot="{ errors }"
                      rules="max:500"
                      name="Comentarios"
                    >
                      <label class="input-label">
                        Comentarios:
                      </label>
                      <v-textarea
                        v-model="comentarios"
                        :error-messages="errors"
                        filled
                        name="input-7-4"
                        color="success"
                      />
                    </validation-provider>
                    <div class="crt-form-bottom">
                      <div class="crt-form-bottom__captcha">
                        <vue-recaptcha
                          sitekey="6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI"
                          @verify="onVerify"
                          @expired="onExpired"
                        />
                      </div>
                      <div class="crt-form-bottom__btn">
                        <v-btn
                          color="success"
                          default
                          type="submit"
                          :disabled="invalid"
                          :loading="btnLoader"
                        >
                          Enviar
                        </v-btn>
                        <fade-transition>
                          <div class="captcha-alert">
                            <v-alert
                              v-show="showRecaptchaAlert"
                              dense
                              text
                              type="warning"
                              class="box"
                            >
                              Complete el Captcha
                            </v-alert>
                          </div>
                        </fade-transition>
                      </div>
                    </div>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
            <splitter />
            <v-row class="charla_row__bottom-btn pa-0">
              <v-col class="col-12 pa-0 text-center">
                <v-btn
                  class="warning"
                  to="/charlas-talleres"
                >
                  Ver Listado de Charlas y Talleres
                </v-btn>
              </v-col>
            </v-row>
          </form>
        </validation-observer>
      </div>
      <div v-else>
        <v-row>
          <v-col class="text-center">
            <v-progress-circular
              :size="70"
              color="success"
              indeterminate
              width="5"
            />
          </v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>
<script>
// import { mapState } from 'vuex'
  import Vue from 'vue'
  import VueRecaptcha from 'vue-recaptcha'
  import { FadeTransition } from 'vue2-transitions'
  export default {
    name: 'Charla',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      VueRecaptcha,
      FadeTransition,
    },
    data () {
      return {
        charla: '',
        charlasObjetivos: '',
        charged: false,
        btnLoader: false,
        showRecaptchaAlert: false,
        recaptchaVerified: false,
        // Formulario
        nombreCompleto: '',
        rut: '',
        comentarios: '',
        fechaNacimiento: '',
        email: '',
        telefono: '',
        toLink: this.$route.params.toLink,
      }
    },
    computed: {
      computedOrdenarFecha () {
        return this.ordenarFecha(this.fechaNacimiento)
      },
    },
    watch: {
      recaptchaVerified: function () {
        if (this.recaptchaVerified) {
          this.hideMsg()
        }
      },
      rut: function () {
        this.rut = this.rut.replace(/^0+|[^0-9kK]+/g, '').toUpperCase()
        const len = this.rut.length - 1
        if (len > 3) {
          const dv = this.rut[len]
          const beforeDv = this.rut
            .substring(0, len)
            .replace(/\B(?=(\d{3})+(?!\d))/g, '.')
          this.rut = beforeDv + '-' + dv
        }
      },
      '$route.params.toLink': {
        handler: function (toLink) {
          this.getCharla(toLink)
        },
      },
    },
    created () {
      this.getCharla(this.$route.params.toLink)
      this.getCharlaObjetivos(this.$route.params.toLink)
    },
    methods: {
      getCharla (toLink) {
        const body = {
          toLink,
        }
        Vue.axios
          .post('/api/charlas/charla', body)
          .then(response => {
            this.charla = response.data.result[0]
            // this.charged = true
          })
          .catch(err => {
            console.log(err)
          })
      },
      getCharlaObjetivos (toLink) {
        const body = {
          toLink,
        }
        Vue.axios
          .post('/api/charlas/objetivos', body)
          .then(response => {
            this.charlasObjetivos = response.data.objetivos
            console.log(this.charlasObjetivos)
            this.charged = true
          })
          .catch(err => {
            console.log(err)
          })
      },
      onVerify () {
        if (!this.recaptchaVerified) {
          this.recaptchaVerified = true
        }
      },
      onExpired () {
        this.recaptchaVerified = false
      },
      hideMsg () {
        this.showRecaptchaAlert = false
      },
      async validateForm () {
        const isValid = await this.$refs.observer.validate()
        if (!isValid) {
          return false
        }

        if (!this.recaptchaVerified) {
          this.showRecaptchaAlert = true
          return false
        }

        // console.log('validando')
        this.btnLoader = true
        const rutCompleto = this.rut
          .split('.')
          .join('')
          .trim()
        const [rut, digito] = rutCompleto.split('-')

        const body = {
          nombreCompleto: this.nombreCompleto,
          rut,
          digito,
          comentarios: this.comentarios,
          fechaNacimiento: this.fechaNacimiento,
          email: this.email,
          telefono: this.telefono,
          toLink: this.toLink,
        }

        // console.log(body)

        Vue.axios
          .post('/api/formularios/inscripcion-charla', body)
          .then(response => {
            // console.log(response)
            // this.showEnviadoMsg()
            this.limpiarFormulario()
            this.btnLoader = false
            this.$swal({
              icon: 'success',
              title: 'Listo.',
              text: 'Solicitud Enviada!',
            })
          })
          .catch(error => {
            console.log(error.message)
          })
      },
      limpiarFormulario () {
        this.nombreCompleto = ''
        this.rut = ''
        this.comentarios = ''
        this.fechaNacimiento = ''
        this.email = ''
        this.telefono = ''
        requestAnimationFrame(() => {
          // Reseteamos vee
          this.$refs.observer.reset()
        })
        this.onExpired() // Reseteamos captcha
      },
      ordenarFecha (fecha) {
        if (!fecha) return null

        const [year, month, day] = fecha.split('-')
        return `${day}/${month}/${year}`
      },
    },
    metaInfo: {
      title: 'Charla',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
